  95% {
    opacity: 1;
    transform: translate(0px, 75px);
  }
  100% {
    opacity: 0;
    transform: translate(0px, 75px);
  }
}
.video-section {
  position: relative;
  height: calc(100vh - 55px);
}
.video-section video {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.video-section h2 {
  position: absolute;
  top: calc((100vh - 55px) / 2);
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 2.4em;
  z-index: 10;
  text-align: center;
  font-family: "ibm-plex-mono", sans-serif;
  font-weight: 700;
  font-style: normal;
  color: #fff;
}
@media only screen and (min-width: 40em) {
  .video-section h2 {
    font-size: 3em;
    text-align: left;
  }
}
.video-overlay {
  z-index: 5;
}
